      });
    },
    //id ,分數資料(歷史分數 構面名稱 分數上限 分數間距)  圖表設定(歷史分數 實際得分 構面名稱)
    line(ctx, data, data2) {
      let scoreArr = [];
      let subjectArr = [];
      let historyArr = [];
      let historyArr2 = [];
      let chartTitle = data.chartTitle;
      let maxscore = data.maxscore;
      let scoreRange = data.scoreRange;
      $.each(data.dimensions, function (key, value) {
        subjectArr.push(value.subjectArr);
        historyArr.push(value.historyArr);
        historyArr2.push(value.historyArr2);
      });
      //自己的分數 (進行構面比對 一樣的為該構面的得分)
      $.each(data2, function (key, value) {
        $.each(subjectArr, function (key2, value2) {
          if (value.subjectArr == value2[0]) {
            scoreArr.push(value.myscoreArr);
          }
        });
      });
      let color1 = "#ffb81c"

      if (this.blue_checkbox) {
        color1 = "#d8973d"
      }

      var marksData = {
        labels: subjectArr,
        datasets: [
          {
            label: "我的分數",
            borderColor: color1,
            data: scoreArr,
            fill: false,
          },
        ],
      };
      if (historyArr.length > 0) {
        let dataset = {}
        if (this.blue_checkbox) {
          dataset = {
            label: "產業平均",
            backgroundColor: "#486996",
            borderColor: "#486996",
            data: historyArr
          };
        } else {
          dataset = {
            label: "產業平均",
            backgroundColor: "#00816d",
            borderColor: "#00816d",
            data: historyArr
          };
        }

        marksData.datasets.push(dataset);
      }
      if (historyArr2.length > 0) {
        if (historyArr2[0] !== "0" && historyArr2[0] !== "") {
          let dataset = {
            label: "同業平均",
            backgroundColor: "#7f7f7f",
            borderColor: "#7f7f7f",
            data: historyArr2
          };
          marksData.datasets.push(dataset);
        }
      }
      var chartOptions = {
        legend: {
          display: true,
          position: 'bottom',
          maxHeight: 10,
          labels: {
            boxWidth: 10,
            font: {
              size: 2
            },
            padding: 2
          }
        },
        responsive: true,
        maintainAspectRatio: false,
        tooltips: {
          mode: "index",
          intersect: false,
        },
        hover: {
          mode: "nearest",
          intersect: true,
        },
        title: {
          display: true,
          text: "",
        },
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
                // fontSize: 10,
                min: 0,
                max: parseFloat(maxscore),
                stepSize: parseFloat(scoreRange)
              },
              scaleLabel: {
                display: true,
                labelString: "分數",
              },
            },
          ],
          xAxes: [
            {
              ticks: {
                // fontSize: 12,
              },
            },
          ],
        },
      };
      this.html_chart = new Chart(ctx, {
        type: "line",
        data: marksData,
        options: chartOptions,
        responsive: true,
        maintainAspectRatio: false,
      });
    },
    //id ,分數資料(歷史分數 構面名稱 分數上限 分數間距)  圖表設定(歷史分數 實際得分 構面名稱)
    radar(ctx, data, data2) {
      let scoreArr = [];
      let subjectArr = [];
      let historyArr = [];
      let historyArr2 = [];
      let chartTitle = data.chartTitle;
      let maxscore = data.maxscore;
      let scoreRange = data.scoreRange;
      $.each(data.dimensions, function (key, value) {
        subjectArr.push(value.subjectArr);
        historyArr.push(value.historyArr);
        historyArr2.push(value.historyArr2);
      });
      //自己的分數 (進行構面比對 一樣的為該構面的得分)
      $.each(data2, function (key, value) {
        $.each(subjectArr, function (key2, value2) {
          if (value.subjectArr == value2[0]) {
            scoreArr.push(value.myscoreArr);
          }
        });
      });
      let color1 = "#ffb81c"

      if (this.blue_checkbox) {
        color1 = "#d8973d"
      }

      var marksData = {
        labels: subjectArr,
        datasets: [
          {
            label: "我的分數",
            borderColor: color1,
            data: scoreArr,
            fill: false,
          },
        ],
      };
      if (historyArr.length > 0) {
        let dataset = {
          label: "產業平均",
          borderColor: "#00816d",
          data: historyArr
        };
        marksData.datasets.push(dataset);
      }
      if (historyArr2.length > 0) {
        if (historyArr2[0] !== "0" && historyArr2[0] !== "") {
          let dataset = {
            label: "同業平均",
            borderColor: "#7f7f7f",
            data: historyArr2
          };
          marksData.datasets.push(dataset);
        }
      }
      var chartOptions = {
        legend: {
          display: true,
          position: 'bottom',
          maxHeight: 10,
          labels: {
            boxWidth: 10,
            font: {
              size: 2
            },
            padding: 2
          }
        },
        responsive: true,
        maintainAspectRatio: false,
        tooltips: {
          mode: "index",
          intersect: false
        },
        hover: {
          mode: "nearest",
          intersect: true
        },
        title: {
          display: true,
          text: ""
        },
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
                // fontSize: 10,
                min: 0,
                max: parseFloat(maxscore),
                stepSize: parseFloat(scoreRange)
              },
              scaleLabel: {
                display: true,
                labelString: "分數"
              }
            }
          ],
          xAxes: [
            {
              ticks: {
                // fontSize: 12
              }
            }
          ]
        }
      };
      this.html_chart = new Chart(ctx, {
        type: "radar",
        data: marksData,
        options: chartOptions,
        responsive: true,
        maintainAspectRatio: false
      });
    },
  },
};
</script>

