  padding-bottom: 4px;
  font-size: 25px;
}

font {
  font-family: Microsoft JhengHei;
}

.strong_font {
  color: #1e7fc8;
  margin-left: -4px;
}

.back-home {
  font-size: 0.8em;
}
</style>



